.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    margin-top: 15px;
}

.header-img {
    height: 60px;
    margin-left: 60px;
}

.header-nav {
    list-style: none;
    display: flex;
    position: relative;
}

.nav-option {
    color: var(--color-secundary);
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 500;
    margin-right: 60px;
}

.nav-option:hover {
    color: var(--color-secundary-hover);
}